@import url("https://fonts.googleapis.com/css2?family=Source+Sans+Pro:wght@600&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Playfair+Display+SC&display=swap");
@import url("https://fonts.googleapis.com/css2?family=League+Spartan:wght@500&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Titillium+Web:wght@600&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Lobster+Two:wght@700&family=Titillium+Web:wght@600&display=swap");

h1 {
  margin: 0;
}

p {
  margin: 0;
  font-family: League Spartan;
  font-weight: 500;
}

.toggle-control {
  position: relative;
  padding: 0 4px;
  display: flex;
  align-items: center;
}

input[type='checkbox'].dmcheck {
  width: 40px;
  height: 10px;
  background: #555;
  position: relative;
  border-radius: 5px;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  cursor: pointer;
  vertical-align: 2px;
  outline: none;
}
  input:checked + label {
    left: 30px;
  }

  input:focus-visible {
    outline: solid 2px white;
  }

  input + label {
    display: inline-block;
    width: 18px;
    height: 18px;
    border-radius: 50%;
    transition: all 0.3s ease;
    cursor: pointer;
    position: absolute;
    left: 2px;
    background: #fff;
    opacity: 0.9;
    background-color:rgb(200, 200, 200);
  }



body.light-mode {
  background-image: url("https://static.wixstatic.com/media/a67ef4_425b83e6266a4eaa9795d307a5c6bc60~mv2.png");
  background-position: center;
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-size: cover;
  color: black;
  transition: background-color 0.3s ease;
}

body.light-mode a {
  text-decoration-line: none;
  color: black;
}

body.light-mode .linkedin {
  opacity: 0.8;
}

body.light-mode .Art {
  opacity: 0.9;
}

body.dark-mode {
  background-image: url("https://static.wixstatic.com/media/a67ef4_28345fde0cf342ea818523db9fc69f3a~mv2.png");
  background-position: center;
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-size: cover;
  background-color: black;
  border-color: white;
  color: white;
  transition: background-color 0.3s ease;
}

body.dark-mode a {
  text-decoration-line: none;
  color: white;
  z-index: 1;
}

body.dark-mode p {
  color: white;
}

body.dark-mode div, body.dark-mode span {
  border-color: rgb(255, 255, 255, 0.2);
}

body.dark-mode .button-89 {
  color: white;
}

body.dark-mode .linkedin {
  background-color: black;
}

body.dark-mode .Propos {
  background-color: rgb(0, 0, 0, 0.8);
}

body.dark-mode .Text1  {
  background-color: rgb(0, 0, 0, 0.8);
}

body.dark-mode .linkedin {
  opacity: 1;
}

body.dark-mode .Art {
  opacity: 1;
}

body.dark-mode .Text {
  background-color: rgb(0, 0, 0, 0.8);
}

.button-89 {
  --b: 3px;   /* border thickness */
  --s: .45em; /* size of the corner */
  --color: #373B44;
  
  padding: calc(.5em + var(--s)) calc(.9em + var(--s));
  color: var(--color);
  --_p: var(--s);
  background:
    conic-gradient(from 90deg at var(--b) var(--b),#0000 90deg,var(--color) 0)
    var(--_p) var(--_p)/calc(100% - var(--b) - 2*var(--_p)) calc(100% - var(--b) - 2*var(--_p));
  transition: .3s linear, color 0s, background-color 0s;
  outline: var(--b) solid #0000;
  outline-offset: .6em;
  font-size: 16px;

  border: 0;

  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;

  cursor: pointer;
}

.button-89:hover,
.button-89:focus-visible{
  --_p: 0px;
  outline-color: var(--color);
  outline-offset: .05em;
}

.button-89:active {
  background: var(--color);
  color: #fff;
}